.navbar-comp {
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  left: 0;
  display: flex;
  z-index: 10;
  background: #1f7043;
  padding: 18px 15px;
  justify-content: space-between;
  box-shadow: 0 2px 6px #1f7043; }
  .navbar-comp .main-link {
    font-size: 50px;
    color: #ffffffdc;
    font-weight: bold; }
    @media only screen and (max-width: 750px) {
      .navbar-comp .main-link {
        font-size: 22px; } }
  .navbar-comp .navbar-links {
    font-size: 20px;
    display: flex;
    align-items: center;
    color: #dfdfdfdc; }
    .navbar-comp .navbar-links .nav-link {
      padding-left: 18px;
      text-align: right;
      transition: all 0.3s ease-in-out; }
      .navbar-comp .navbar-links .nav-link:hover {
        color: white; }
    @media only screen and (max-width: 750px) {
      .navbar-comp .navbar-links {
        font-size: 14px; } }

