.footer-comp {
  display: flex;
  background: #1f7043;
  padding: 18px 15px;
  justify-content: space-between;
  color: white;
  z-index: 10; }
  .footer-comp .copyright {
    font-size: 16px; }
  .footer-comp .footer-links {
    font-size: 15px;
    display: flex;
    align-items: center;
    color: #dfdfdfdc; }
    .footer-comp .footer-links > a {
      padding: 0 3px; }
      .footer-comp .footer-links > a:hover {
