.view-comp {
  padding-top: 10px; }
  .view-comp > h2 {
    padding: 10px 15px;
    letter-spacing: 3px; }
  .view-comp > h3 {
    background: #99AAB5;
    padding: 15px;
    letter-spacing: 1px;
    margin: 5px 0; }
  .view-comp .note {
    border: 1px solid black;
    width: 80%;
    padding: 10px 15px;
    margin: 10px 0;
    background: #23272A;
    max-width: 600px; }
    @media only screen and (max-width: 750px) {
      .view-comp .note {
        width: 100%; } }
  .view-comp .section {
    padding: 0 15px;
    max-width: 100%; }
    .view-comp .section img {
      margin: 5px 0;
      width: 80%;
      min-width: 400px;
      max-width: 600px; }
      .view-comp .section img.img-small {
        width: 40%;
        max-width: 300px; }
    .view-comp .section > div {
      color: #eaeaeaea; }
    @media only screen and (max-width: 750px) {
      .view-comp .section img {
        width: 100%;
        min-width: 250px; } }

