/* The sidebar menu */
.sidenav {
  max-height: 100%;
  position: -webkit-sticky;
  position: sticky;
  z-index: 1;
  /* Stay on top */
  top: 95px;
  /* Stay at the top */
  left: 0;
  background-color: #23272A;
  /* Black */
  overflow-x: hidden;
  /* Disable horizontal scroll */
  padding-top: 10px;
  transition: all 0.3s ease-in-out; }
  @media only screen and (max-width: 750px) {
    .sidenav {
      margin-left: -200px;
      top: 62px; } }
  .sidenav .sidenav-active {
    pointer-events: none; }
    .sidenav .sidenav-active .sidenav-link {
      background: #2C2F33; }
      .sidenav .sidenav-active .sidenav-link img {
        background: #23272A; }
  .sidenav .sidenav-link {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 6px 6px 6px 15px;
    font-size: 22px;
    transition: all 0.3s ease-in-out; }
    .sidenav .sidenav-link:hover {
      background: #7289DA; }
      .sidenav .sidenav-link:hover img {
        background: #23272A; }
    .sidenav .sidenav-link img {
      width: 36px;
      height: 36px;
      border-radius: 50%;
      background: #2C2F33; }
      @media only screen and (max-width: 750px) {
        .sidenav .sidenav-link img {
          width: 30px;
          height: 30px; } }

