.documentation-comp {
  color: white;
  min-height: 100%;
  display: grid;
  grid-template-columns: 240px 1fr;
  position: relative; }
  @media only screen and (max-width: 750px) {
    .documentation-comp {
      grid-template-columns: 40px 1fr; } }
  .documentation-comp .sidenav-wrapper {
    height: 100%;
    background: #23272A; }
  .documentation-comp .documentation-content {
    background: #2C2F33;
    height: 100%; }

